.carousel {
    position: relative;
    height: 415px;

    &__image {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 25px;
    }

    &__arrow {
        position: absolute;
        top: 0;
        bottom: 0;
        margin: auto 50px;
        cursor: pointer;

        &__left {
            left: 0;
        }

        &__right {
            right: 0;
        }
    }

    &__pagination {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 25px;
        text-align: center;
        color: $color-4;
        font-size: 24px;
    }
}