.cards_all {
    position: relative;

    &__text {
        font-size: 18px;
        font-weight: 500;
        position: absolute;
        margin: auto 20px;
        bottom: 20px;
        color: #FFFFFF;
    }

    &__card {
        display: block;
        width: 100%;
        height: 340px;
        border-radius: 10px;
        object-fit: cover;
    }
}