.collapse {
    display: flex;
    flex-direction: column;
    width: 100%;

    &__header {
        background-color: $color-1;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-radius: 5px;
        padding: 10px 20px;
        cursor: pointer;

        &:hover {
            background-color: #f78080;
        }
    }

    &__title {
        color: white;
        font-size: 24px;
    }

    &__icon {
        width: 32px;
    }

    &__content {
        width: 100%;
        height: 100%;
        background-color: $color-3;
        border-radius: 10px 10px 5px 5px;
        max-height: 0;
        overflow: hidden;
        transition: max-height 0.3s ease-in-out;

        p {
            font-size: 18px;
            color: #FF6060;
            padding: 15px;
            line-height: 25px;
        }
    }

    &--active &__content {
        max-height: 375px;
    }

    &__icon {
        transition: transform 500ms;
    }

    &--active &__icon {
        transform: rotate(-180deg);
    }

    &__house {
        display: flex;
        flex-direction: row;
        width: 100%;
        gap: 30px;
        justify-content: space-around;
        margin-bottom: 40px;
    }

    li {
        list-style-type: none;
    }
}